import React from 'react';
import './SubTotal.css';
import {getBasketTotalPrice} from '../hooks/reducer';
import {useStateValue} from '../context/ContextProvider';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import CurrencyFormat from 'react-currency-format';
const SubTotal = ({payment}) => {
    const [{basket}] = useStateValue();
    return (
        <div className='subtotal'>
            <Card>
                <Card.Body>
                    <Card.Title>Your Shopping Basket</Card.Title>
                    <Card.Subtitle className="mb-2 text-muted">You have {basket?.length} picked products</Card.Subtitle>
                    <Card.Text>
                    <CurrencyFormat 
				        renderText={(value) => (
                                <p> SubTotal: <strong>{`${value}`}</strong></p>
				        )}
                        decimalState={2}
                        value={getBasketTotalPrice(basket)}
                        displayType={"text"}
                        thousandSeparator={true}
                        prefix={"$"}
			        />
                    </Card.Text>
                    <Card.Link><Button onClick={payment}>Redirect to Payment Page</Button></Card.Link>
                </Card.Body>
            </Card>

        </div>
    )
};
export default SubTotal;